body {
  height: 100%;
  box-sizing: border-box;
  background-color: #222;
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
  overflow: hidden;
}

.canvas-wrapper {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.navbar {
  z-index: 10;
  height: 50px;
  width: 100%;
  opacity: .1;
  border-bottom: 1px solid #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 0;
}

.navbar :hover {
  opacity: 1;
  cursor: pointer;
}

.navbar > h1 {
  color: #fff;
  margin-top: auto;
  margin-bottom: auto;
  font-weight: 900;
}

.buttons {
  position: flex-end;
  right: 10px;
}

.navbar > #githublogo {
  max-width: 32px;
  max-height: 32px;
  margin-top: auto;
  margin-bottom: auto;
  position: absolute;
  right: 10px;
}

#menu {
  transform-origin: center;
  z-index: 2;
  opacity: 0;
  background-color: #2b2a2a;
  border-radius: 5px;
  padding: 10px 0;
  transition: transform .2s, opacity .2s;
  display: block;
  position: absolute;
  transform: scale(0);
  box-shadow: 10px 10px 5px #0000002e;
}

#menu.show {
  opacity: 1;
  transform-origin: 0 0;
  transform: scale(1);
}

.menu-item {
  color: #fff;
  padding: 10px 30px;
  transition: all .1s;
  display: block;
}

.menu-item:hover {
  cursor: pointer;
  background-color: #111;
}

.navbar > h1 > sup {
  font-size: x-small;
  font-weight: 600;
}

#snackbar {
  visibility: hidden;
  min-width: 250px;
  color: #fff;
  text-align: center;
  z-index: 1;
  background-color: #292929;
  border-radius: 10px;
  margin-left: -125px;
  padding: 16px;
  font-size: 17px;
  position: fixed;
  bottom: 30px;
  left: 50%;
  box-shadow: 10px 10px 5px #0000002e;
}

#snackbar.show {
  visibility: visible;
  animation: .5s fadein;
}

@keyframes fadein {
  from {
    opacity: 0;
    bottom: 0;
  }

  to {
    opacity: 1;
    bottom: 30px;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
    bottom: 30px;
  }

  to {
    opacity: 0;
    bottom: 0;
  }
}

#shortcuts {
  visibility: hidden;
  min-width: 50%;
  color: #fff;
  text-align: center;
  z-index: 1;
  opacity: .7;
  background-color: #292929;
  border-radius: 10px;
  padding: 16px;
  font-size: 17px;
  display: grid;
  position: absolute;
  top: 60px;
  bottom: 30px;
  left: 50%;
  transform: translate(-50%);
  box-shadow: 10px 10px 5px #0000002e;
}

#shortcuts > #close_shortcut {
  position: absolute;
  top: 15px;
  right: 15px;
}

#shortcuts > #close_shortcut:hover {
  cursor: pointer;
}

#shortcuts.show {
  visibility: visible;
}

#shortcuts > h1 {
  grid-area: 1 / 1 / auto / span 3;
}

.sct_elem > .sct {
  font-weight: 600;
}

.sct_elem > .sct_desc {
  font-weight: 100;
}

